body.cfl-wrapper {
  color: $black;
  font-family: $font-gotham-sans;
}

.title {
  font-weight: 700;
  line-height: 0.83;
  font-size: rem(60); // 60px
  text-transform: uppercase;
  font-family: $font-gotham-headline;

  @include media-breakpoint-up(xl) {
    font-size: rem(96); // 96px
  }
}

.title-xl {
  @extend .title;

  @include media-breakpoint-up(xl) {
    line-height: 0.85;
    font-size: rem(120); // 120px
  }
}

.subtitle {
  font-weight: 400;
  line-height: 1.58;
  font-size: rem(24); // 24px
  font-family: $font-gotham-headline;

  @include media-breakpoint-up(xl) {
    line-height: 1.62;
    font-size: rem(34); // 34px
  }
}

.subtitle-xl {
  font-weight: 400;
  line-height: 1.25;
  font-size: rem(30); // 30px
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    font-size: rem(48); // 48px
  }
}

.subtitle-xxl {
  font-weight: 400;
  line-height: 1.22;
  font-size: rem(36); // 36px
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    line-height: 1.17;
    font-size: rem(60); // 60px
  }
}

.text-body-xs {
  line-height: 1.17;
  font-size: rem(12); // 12px
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    font-weight: 500;
    line-height: 1.55;
    font-size: rem(14); // 14px
  }
}

.text-body-sm {
  font-family: $font-gotham-sans;
  font-size: rem(14); // 14px
  line-height: 1.57;
  font-weight: 500;
}

.text-body-md {
  font-weight: 400;
  font-size: rem(16); // 16px
  line-height: 1.625;
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    line-height: 1.5;
  }
}

.text-body {
  font-weight: 400;
  line-height: 1.56;
  font-size: rem(18);
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    line-height: 1.6;
    font-size: rem(20);
  }
}

.text-body-xl {
  font-family: $font-gotham-sans;
  font-weight: 400;
  line-height: 1.45;
  font-size: rem(22); // 22px

  @include media-breakpoint-up(xl) {
    line-height: 1.43;
    font-size: rem(28); // 28px
  }
}

.text-body-xxl {
  font-weight: 400;
  line-height: 1.45;
  font-size: rem(38); // 38px
  font-family: $font-gotham-sans;

  @include media-breakpoint-up(xl) {
    line-height: 1.23;
    font-size: rem(48); // 48px
  }
}
